<template>
  <v-data-table-server density="compact" class="elevation-2 rounded pt-2 pb-4" :headers="tableData.headers"
    :items="tableData.data" :items-length="tableData.totalItems" :items-per-page="tableData.itemsPerPage"
    no-data-text="No Records Available" v-bind="$attrs" hide-default-footer>
    <template v-for="(index, name) in $slots" v-slot:[name]="data">
      <slot :name="name" v-bind="data"></slot>
    </template>
    <template v-slot:bottom>
      <v-pagination color="primary" v-model="pageNumber" :length="tableData.paginationContext.totalPages" @update:model-value="changePage" />
    </template>
  </v-data-table-server>
</template>

<script setup>
import { ref, defineEmits } from 'vue';

const props = defineProps({
  tableData: {},
});

const pageNumber = ref(props.tableData.paginationContext.pageNumber);

const emit = defineEmits(["pageChanged"]);

// function onUpdateOptions(ev) {
//   emit("onUpdateOptions", ev);
// }

function changePage() {
  emit("pageChanged", pageNumber.value);
}

// console.log(props.tableData.data);
</script>
